<template>
    <div v-if="ownerYacht" class="card">
  <div class="card-image">
    <figure class="image is-4by3">
      <img :src="ownerYacht.imgs[0]" alt="Placeholder image">
    </figure>
  </div>
  <div class="card-content">
    <div class="media">
      <div class="media-left">
        <figure class="image is-48x48">
          <img src="https://bulma.io/images/placeholders/96x96.png" alt="Placeholder image">
        </figure>
      </div>
      <div class="media-content">
        <p class="title is-4">John Smith</p>
        <p class="subtitle is-6">@johnsmith</p>
      </div>
    </div>

    <div class="content">
      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
      Phasellus nec iaculis mauris. <a>@bulmaio</a>.
      <a href="#">#css</a> <a href="#">#responsive</a>
      <br>
      <time datetime="2016-1-1">11:09 PM - 1 Jan 2016</time>
    </div>
  </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            ownerYacht: null
        }
    },
    async created() {
        const ownerYachtId = this.$route.params.id;
        try {
            this.ownerYacht = await this.$store.dispatch({type: "loadYacht", yachtId: ownerYachtId})
        } catch (err) {
            console.log('Could not find ownerYacht error:', err)
        }
    },
    methods: {
        goBack() {
            this.$router.push('/yachts')
        }
    }
}
</script>
